import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import IndexHome from '@/components/IndexHome'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Index,
      children: [
        {
          path: '',
          name: 'index',
          component: IndexHome
        },
        {
          path: '/home',
          name: 'index',
          component: IndexHome
        },
        {
          path: '/about',
          name: 'index',
          component: IndexHome
        },
        {
          path: '/download',
          name: 'index',
          component: IndexHome
        },
        {
          path: '/service',
          name: 'index',
          component: IndexHome
        },
        {
          path: '/resume',
          name: 'index',
          component: IndexHome
        }
      ]
    }
  ]
})



